


































var utils = require('../../common/utils').utils;
export default {
	data() {
		return {
			title: 'textarea',
			focus: false,
			num: 0,
			reply_desc: '',
			imageList: [],
			reply_imgs: '',
			to_user: '',
			rec_id: '',
			tempFilePaths: '',
		}
	},
	onLoad: function (options) {
		var _this = this
		_this.rec_id  = options.rec_id;
		_this.to_user = options.to_user;
	},
	methods: {
		// 删除图片
		getRecord(event){
			var indx = event.target.dataset.idx;
			this.imageList.splice(indx,1)
		},
		getLeng(e){
			this.num = e.detail.value.length;
			this.reply_desc = e.detail.value;
		},
		chooseImage: async function () {
			if(this.imageList.length === 9){
				let isContinue = await this.isFullImg();
				console.log("是否继续?",isContinue);
				if(!isContinue){
					return;
				}
			}
			uni.chooseImage({  
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				success: (res) => {
					console.log(JSON.stringify(res.tempFilePaths));
					this.tempFilePaths = res.tempFilePaths[0];
					this.imageList = this.imageList.concat(res.tempFilePaths);
				}
			});
		},
		isFullImg:function(){
			return new Promise((res) => {
				uni.showModal({
					content:"已经有9张图片了,是否清空现有图片？",
					success: (e) => {
						if(e.confirm){
							this.imageList = [];
							res(true);
						}else{
							res(false)
						}
					},
					fail:() => {
						res(false)
					}
				})
			})
		},
		//提交过程记录
		getSubmit() {
			if (this.num === 0) {
				uni.showToast({
					title: '请输入回复内容',
					duration: 2000,
					icon: 'none'
				});
			} else {
				var _this = this;
				if (_this.imageList.length === 0 ) {
					var _data = {
						action: 'reply',
						status: 9,
						reply_imgs: _this.reply_imgs,
						reply_desc: _this.reply_desc,
						to_user: _this.to_user,
						rec_id: _this.rec_id
					}
					utils.requestPost('operation/postreply', _data, function(res){
						if(res.code === 0) {
							uni.navigateBack({
								delta: 1
							});
						}
					})
				}else {
					uni.showToast({
						title: '正在上传图片',
						duration: 800,
						icon: 'none'
					})
					uni.uploadFile({
						url: 'https://' + utils.host + '/weixinweb/xiaochengcxufile/1',
						filePath: _this.tempFilePaths,
						name: 'upfile',
						success: (res) => {
							var data = JSON.parse(res.data)
							_this.reply_imgs = data.url
							console.log(_this.reply_imgs)
							var _data = {
								action: 'reply',
								status: 9,
								reply_imgs: _this.reply_imgs,
								reply_desc: _this.reply_desc,
								to_user: _this.to_user,
								rec_id: _this.rec_id
							}
							utils.requestPost('operation/postreply', _data, function(res){
								if(res.code === 0) {
									uni.navigateBack({
										delta: 1
									});
								}
							})
						}
					})
				}
			}
		}
	}
}



